.cameraWrapper {
  height: 100%;
  width: 100%;
}

.cameraFrame {
  height: 90%;
  width: 100%;
}

.captureBtnWrapper {
  height: 10%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.captureBtn {
  height: 50px;
  width: 50px;
  line-height: 100px;
  border-radius: 100%;
  background-color: red;
  border: 1px solid whitesmoke;
  box-shadow: 0 8px 17px 2px rgb(0 0 0 / 14%), 0 3px 14px 2px rgb(0 0 0 / 12%),
    0 5px 5px -3px rgb(0 0 0 / 20%);
}

.goBack {
  position: absolute;
  bottom: 4%;
  left: 2%;
  background: transparent;
  border: none;
  color: black;
  padding: 3px 8px;
}
